<template>
  <div>

    <b-modal visible centered ref="my-modal" hide-footer scrollable title="" class="NoticeRuleModal" @hidden="hideModal">
      <div class="d-block text-center">
        <p class="my-4">1.            .</p>
        <p class="my-4">2.              .</p>
        <p class="my-4">3.          .</p>
        <p class="my-4">4.         .</p>
      </div>
      <b-form-checkbox id="checkbox"
      class = "noticeCheckBox"
      v-model="status"
      name="checkbox"
      value="accepted"
      >

        <p></p>

      </b-form-checkbox>
      <b-button :disabled="!status" class="mt-2" variant="outline-warning" block @click="closeWithAccept"></b-button>
    </b-modal>
  </div>
</template>

<script>
  export default {
    props: {
      show : {
        default : false
      }

    },
    data(){

      return{
        status : false


      }


    },
    watch : {

      show(newval) {

      if(newVal)
      {
          this.$refs['my-modal'].show()
      }
      else
      {
         this.$refs['my-modal'].hide()
         this.$emit('closeNotice')
      }

      }
    },
    methods: {
      closeWithAccept(){

        this.$refs['my-modal'].hide()
        this.$emit('closeWithAccept')


      },
      showModal() {
        this.$refs['my-modal'].show()
      },
      hideModal() {
        this.$refs['my-modal'].hide()
        this.$emit('closeNotice')

      },
    },
    destroyed(){

      this.$emit('closeNotice')
    }
  }
</script>

<style scoped>

p{

  font-weight :bolder;

}

.noticeCheckBox{

  left : 80%;
}
</style>
